import uniNavBar from '@/components/uni/uni-nav-bar/uni-nav-bar.vue'
	export default {
		data() {
			return {
				
			}
		},
		components:{
			uniNavBar
		},
		methods: {
			//点击关闭返回上一页
			goBack(){
				uni.navigateBack({
					delta:1
				})
			},
			goPayment(){
				uni.navigateTo({
					url:'/pages/payment-success/payment-success'
				})
			}
		}
		
	}
